
.wrap {
    margin: 20px 10px;
    font-size: 14px;
    font-family: Arial, sans-serif;
}

.title-invoice {
    font-weight: bold;
    border-bottom: 2px solid #000;
    padding: 10px 10px;
}

.title-sub {
    margin: 20px 10px;
    font-size: 16px;
    display: -ms-flexbox;
    display: flex;
}

.title-sub.customer {
    margin-top: 25px;
}

.title-sub .label {
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 200px;
}

.title-sub .text {
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 700px;
}

@media print
{
    .no-print, .no-print *
    {
        display: none !important;
    }
}


