html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

#root {
  width: 900px;
  margin: 20px auto;
}

.table {
  display: table;
  width: 100%;
}

.table .row {
  display: table-row;
}

.table .cell {
  display: table-cell;
  padding: 15px 0;
  font-size: 16px;
  border-top: 1px solid #000000;
  border-left: 1px solid #000000;
  width: 100%;
}

.table .cell:last-child {
  border-right: 1px solid #000000;
}

.table .row:last-child .cell {
  border-bottom: 1px solid #000000;
}

.table.no-border .row .cell {
  border: none !important;
  padding: 10px 0;
}

.table.no-border {
  margin-left: 20px;
}
.table.no-border.total {
  margin-left: -20px;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.total-box {
  font-size: 16px;
  margin: 15px 0;
}

.button {
  border-radius: 2px;
  vertical-align: top;
  margin-left: 5px;
  font-size: 16px;
  height: 35px;
  border: 1px solid #4646a4;
  width: 100px;
  display: inline-block;
  color: #ffffff;
  background-color: #4646a4;
  cursor: pointer;
}

.button.cancel {
  background-color: #7d7d7d;
}

.content-empty {
  width: 500px;
  margin: 0 auto;
}

.content-empty-block {
  display: inline-block;
  vertical-align: top;
  margin: 150px 0px;
}

.form {
  margin: 20% auto;
  padding: 20px;
  width: 400px;
  border-radius: 3px;
  border: 1px solid #919191;
}

.form input {
  border: 1px solid #c2c2c2;
  border-radius: 2px;
  font-size: 18px;
  margin: 10px;
  width: 200px;
  display: inline-block;
}
.form label {
  width: 50px;
  display: inline-block;
}
.form button {
  border: 1px solid #c2c2c2;
  background-color: #4646a4;
  color: #ffffff;
  font-size: 18px;
  border-radius: 2px;
  margin: 10px;
}

.button.exit {
  position: fixed;
  opacity: 0.3;
  right: 10px;
}

.button.exit:hover {
  position: fixed;
  opacity: 1;
}


